.footer {
  width: 100%;
  background-color: $special-background;
}

.footer__container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 30px;
  padding-right: 30px;
}

.footer__logo {
  display: none;
}

.footer__copyright {
  margin: 0;
  padding: 0;
  padding-top: 22px;
  padding-bottom: 22px;
}

@media (min-width: $tablet-width) {
  .footer__container {
    flex-direction: row;
    justify-content: space-between;
    padding-top: 28px;
    padding-bottom: 26px;
    padding-left: $desktop-padding;
    padding-right: $desktop-padding;
  }

  .footer__logo {
    display: block;
    margin-top: -5px;

    &:hover,
    &:focus {
      fill: $special-green;
    }

    &:active {
      opacity: 0.3;
    }
  }

  .footer__copyright {
    margin-top: 7px;
  }
}
